.header {
  background-color: #f3f7d7;
}
.header-logo {
  margin-left: 20px;
}
.header-top {
  background: #e6e09a;
  font-size: 18px;
  line-height: 24px;
  color: #33312c;
  height: 80px;
  padding-top: 20px;
  padding-bottom: 20px;
}
.main-header {
  display: flex;
  justify-content: space-between;
  width: 1200px;
  margin: 0 auto;
  height: 40px;
}
.header-item {
  display: flex;
  flex-wrap: wrap;
  list-style: none;
  padding: 0;
  margin: 0;
}
.header-item li {
  list-style-type: none;
  /* Убираем маркеры */
}
.dadayan {
  position: relative;
  margin-top: -5px;
  min-width: 170px;
}
.add-number {
  background-color: red;
  height: 50px;
  text-align: center;
  border: none;
  border-radius: 5%;
  padding: 11px 26px;
  margin-top: -5px;
  margin-right: 20px;
  font-weight: 600;
}
.add-number a {
  color: white;
}
.register,
.auto {
  padding: 8px 15px;
}
.register a,
.auto a {
  color: #33312c;
}
.promo__list {
  display: flex;
  justify-content: space-between;
  list-style-type: none;
  padding: 0 20px;
}
.promo__list a {
  font-size: 18px;
  color: #3097b1;
}
.header-nav {
  width: 1200px;
  margin: 0 auto;
}
.scan {
  position: relative;
  padding: 20px;
  max-width: 1200px;
  margin: 0 auto;
}
.scan input[type="search"] {
  padding: 8px 19px 9px 19px;
  border: 1px solid black;
}
.scan .scan-btn {
  background-color: red;
  height: 40px;
  text-align: center;
  border: none;
  border-radius: 8%;
  padding: 9px 26px;
  font-weight: 600;
  color: white;
  margin-left: 40px;
}
.scan .scan-input {
  width: 500px;
}
.scan .scan-price {
  width: 150px;
  margin-left: 10px;
}
.scan .label__price {
  margin-left: 30px;
}
.hr-shelf {
  padding: 0;
  margin-top: -10px;
  height: 20px;
  border: none;
  border-bottom: 1px solid #c7c7c7;
  box-shadow: 0 5px 5px -5px #b3b3b3;
}
.main-like {
  max-width: 1200px;
  margin: 0 auto;
}
.main-like h2 {
  padding-left: 20px;
}
.lots__list {
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
  list-style: none;
  padding-left: 20px;
  padding-bottom: 30px;
}
.lots__list li {
  border: 1px solid #8d8d8d;
  padding: 10px;
  margin-top: 20px;
  min-width: 350px;
}
.container {
  width: 1200px;
  margin: 60px auto;
  padding: 0 15px;
}
.preview {
  display: none;
  position: absolute;
}
.preview__remove {
  position: absolute;
  right: 0;
  padding: 5px 10px;
  font-size: 16px;
  line-height: 16px;
  color: #ffffff;
  border: none;
  background-color: transparent;
}
.form__item--file label span {
  display: block;
  width: 115px;
  height: 110px;
  background-color: #cbdbab;
  color: #ffffff;
  font-size: 16px;
  text-align: center;
  font-weight: 700;
  padding: 45px 0;
  box-sizing: border-box;
  border: 1px solid #cbdbab;
  cursor: pointer;
}
.red__button {
  background-color: red;
  margin-top: 20px;
  padding: 12px 22px;
  color: white;
  border: none;
  font-size: 16px;
  border-radius: 3px;
}
.text-link {
  margin-left: 20px;
}
.form__container input {
  width: 500px;
}
.form__container .message__text {
  width: 500px;
  resize: none;
}
.container-register {
  width: 900px;
  margin: 60px auto;
  padding: 0 15px;
}
.message__text {
  height: 120px;
}
