  height: 100px;
  background-color: #e6e6e6;
  margin-top: 60px;
}
.main__footer {
  display: flex;
  justify-content: space-between;
  flex-direction: row;
  flex-wrap: wrap;
  padding: 0 20px;
  max-width: 1200px;
  margin: 10px auto 0;
}
.social {
  width: 150px;
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
  flex-direction: row;
  list-style: none;
}
.social li {
  padding: 7px;
  width: 45px;
  height: 45px;
  border-radius: 25px;
  border: 1px solid #646464;
}
.footer-login {
  position: absolute;
  margin-top: auto;
  left: 0;
  bottom: 0;
  width: 100%;
}
.add-container {
  width: 1000px;
  margin: 60px auto;
}
.form__item {
  margin: 0;
  margin-bottom: 14px;
  font-size: 14px;
}
.form__item label {
  display: block;
  margin-bottom: 13px;
}
.form__item input,
.form__item textarea {
  display: block;
  padding: 10px 10px;
  border: 1px solid black;
  box-sizing: border-box;
}
.form__container-two {
  display: flex;
  width: 750px;
  justify-content: space-between;
}
.form__item select {
  display: block;
  height: 38px;
  padding: 10px 10px;
  border: 1px solid black;
  box-sizing: border-box;
  border-radius: 0;
}
.form__item--wide {
  width: 750px;
}
.form__item--wide textarea {
  min-height: 160px;
}
.form__input-file {
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
  width: 750px;
}
.add-price {
  width: 120px;
}
.item-name {
  width: 350px;
}
.add-price {
  width: 100px;
}
.description {
  width: 100%;
}
.add__button {
  width: 200px;
}
.login-email,
.login-password {
  width: 350px;
}
.button__login {
  width: 150px;
}
.container-login {
  width: 900px;
  margin: 60px auto;
}
html {
  background-color: white;
  height: 100%;
}
img {
  max-width: 100%;
}
a {
  text-decoration: none;
}
